import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const styles = {
  card: {
    minWidth: 275,
    marginBottom: '20px',
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    marginBottom: 16,
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
};

function SemiPro(props) {
  const { classes } = props;
  return (
    <div className="App-header">
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="headline" component="h2">
            Music Producer
          </Typography>
          <Typography className={classes.pos} color="textSecondary">
            Max Evilmind
          </Typography>
          <Typography className={classes.pos}>
            Over 15 electronic, alternative singles
          </Typography>
          <Typography className={classes.pos}>
            A bunch of collaborations with experienced artists
          </Typography>
          <Typography className={classes.pos}>
            Fruity Loops Studio, Ableton, Cubase
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Learn More</Button>
        </CardActions>
      </Card>
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="headline" component="h2">
            Band Player
          </Typography>
          <Typography className={classes.pos} color="textSecondary">
            Evergreens Band
          </Typography>
          <Typography className={classes.pos}>
            Live concert experience
          </Typography>
          <Typography className={classes.pos}>
            Over 2 years of experience playing in a band of 4-7 people
          </Typography>
          <Typography className={classes.pos}>
            Playing Guitar, Bass, Drums, Keys, Pads
          </Typography>
          <Typography className={classes.pos}>
            Clean Vocals, Extreme Vocals
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Learn More</Button>
        </CardActions>
      </Card>
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="headline" component="h2">
            Music Teacher
          </Typography>
          <Typography className={classes.pos} color="textSecondary">
            Max Evilmind
          </Typography>
          <Typography className={classes.pos}>
            Music Composition with Ableton and Fruity Loops Studio
          </Typography>
          <Typography className={classes.pos}>
            Playing Guitar
          </Typography>
          <Typography className={classes.pos}>
            Lyrics Composition
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Learn More</Button>
        </CardActions>
      </Card>
    </div>
  );
}

SemiPro.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SemiPro);


