import React, { Component } from 'react';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import './Home.css';
import './tiles.css';
import first from './images/1.jpg'
import second from './images/2.jpg'
import third from './images/3.jpg'
import fourth from './images/4.jpg'
import fifth from './images/5.jpg'
import sixth from './images/6.jpg'

const styles = theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: '100%',
  },
  subheader: {
    width: '100%',
  },
  titleBar: {
    background:
      `linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)`
  }
});

const tileData = [
  {
    img: first,
    title: 'Value Delivery',
    author: 'author',
    cols: 2,
    description: 'Delivers value by applying advanced technology',
  },
  {
    img: second,
    title: 'Creativity',
    author: 'author',
    cols: 1,
    description: 'Uses imagination a lot',
  },
  {
    img: third,
    title: 'Ambition',
    author: 'author',
    cols: 1,
    description: 'Ambitious about many different things',
  },
  {
    img: fourth,
    title: 'Communication',
    author: 'author',
    cols: 1,
    description: 'Speaks English, German, Russian',
  },
  {
    img: fifth,
    title: 'Theory',
    author: 'author',
    cols: 1,
    description: 'Fluent in Algorithms and Data Structures. Applied Mathematics and Data Science',
  },
  {
    img: sixth,
    title: 'Practice',
    author: 'author',
    cols: 2,
    description: 'Over a year of experience working in Silicon Valley, London, Singapore',
  }
];

class Home extends Component {
  render() {
    const { classes } = this.props;
    return (
      <div className="App">
        <header className="App-header">
          <Card className={classes.card}>
            <CardContent>
              <Typography variant="headline" component="h2">
                Max Larionov
              </Typography>
              <Typography className={classes.pos} color="textSecondary">
                November 14 1997
              </Typography>
              <Typography className={classes.pos} color="textSecondary">
                Bachelor's Degree in Software Engineering
              </Typography>
              <Typography component="p">
                Allround Software Engineering Specialist with a flair of Compliance who has an excellent track record of flourishing in the Technology sector. With my hands-on mentality and result oriented mindset I accomplish to succeed, to stay focused and to deliver beyond expectations. I have proven experience in Full Stack Software Engineering working on the valuable and technically advanced products from dynamically growing startups of Silicon Valley, Singapore and United Kingdom.
                Bilingual proficiency in English and Russian, limited working proficiency in German.
              </Typography>
            </CardContent>
            <CardActions>
              <Button size="small">Learn More</Button>
            </CardActions>
          </Card>
        </header>
        <GridList id="skills" cellHeight={300} className={classes.gridList} cols={4}>
          {tileData.map(tile => (
            <GridListTile key={tile.img} cols={tile.cols || 1} className="skill">
              <Typography variant="headline" component="p">
                {tile.description}
              </Typography>
              <img src={tile.img} alt={tile.title} />
              <GridListTileBar
                title={tile.title}
                classes={{
                  root: classes.titleBar,
                  title: classes.title,
                }}
                className={classes.stripes}
              />
            </GridListTile>
          ))}
        </GridList>
      </div>
    );
  }
}

export default withStyles(styles)(Home);



