import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const styles = {
  card: {
    minWidth: 275,
    marginBottom: '20px',
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    marginBottom: 16,
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  }
};

function Experience(props) {
  const { classes } = props;
  return (
    <div className="App-header">
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="headline" component="h2">
            Full Stack Web Developer, MadDevs.io
          </Typography>
          <Typography className={classes.pos} color="textSecondary">
            Bishkek, Kyrgyzstan — August 2017 - present
          </Typography>
          <Typography className={classes.pos}>
            Eatigo, Singapore - Developed and integrated Billing System. ES6 React + Redux, Jest + Enzyme unit testing.
          </Typography>
          <Typography className={classes.pos}>
            Zentist.io, Silicon Valley - Making dental care in San Francisco more affordable. Our patients save an average of $23,000 a year on dental care. ES6 React + GraphQL/Python/Flask backend. Cucumber and Puppeteer testing.
          </Typography>
          <Typography className={classes.pos}>
            Teacherly.io, London - Providing a superlative teaching experience for teachers by harnessing the power of technology to improve learning. ES6 React + Redux, SCSS, Webpack, Vagrant, Gulp.
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Learn More</Button>
        </CardActions>
      </Card>
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="headline" component="h2">
            Data Scientist, HydraData
          </Typography>
          <Typography className={classes.pos} color="textSecondary">
            Bishkek, Kyrgyzstan — September 2016 - May 2017
          </Typography>
          <Typography>
            <br/> Senior Research Project. Developed a natural language financial assistant for Telegram. Among used technologies are: Python, MongoDB, Scikit, Tensorflow, Keras, Scalable and distributable microservice architecture using Flask, Telegram bot API.
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Learn More</Button>
        </CardActions>
      </Card>
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="headline" component="h2">
            Full Stack Developer, Indycate.me
          </Typography>
          <Typography className={classes.pos} color="textSecondary">
            Bishkek, Kyrgyzstan — June 2016 - January 2017
          </Typography>
          <Typography>
            <br/> Inhouse alternative for Qualtrics. MEAN stack (MongoDB, Express, Angular, Node), Azure Cloud Services, Linux.
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Learn More</Button>
        </CardActions>
      </Card>
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="headline" component="h2">
            Full Stack Developer, Freelance
          </Typography>
          <Typography className={classes.pos} color="textSecondary">
            Bishkek, Kyrgyzstan
          </Typography>
          <Typography>
            <br/> Developed boring aplication
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Learn More</Button>
        </CardActions>
      </Card>
    </div>
  );
}

Experience.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Experience);


